                </div>
            </div>
        </div>
        <div class="newList-content">
            <div class="clearfix">
                <div class="new-list-left-box fl clearfix">
                     
                    <div class="list-item fl" v-for="(item,index) in GameData" :key="index" @click="goDetail(item.id)">
                        <div class="left-img">
                            <el-image class=" err-imgs">
                                <div slot="error" class="image-slot">
                                    <i class="el-icon-picture-outline"></i>
                                </div>
                            </el-image>
