                         <div class="other-box-three">
                            <div class="car-item">
                                <a href="http://web.4399.com/rxlz2/" target="_blank" >
                                    <img class="auto-img" src="https://i02piccdn.sogoucdn.com/429d2ed8ba27a731" alt="">
                                </a>
                                <div class="text">广告</div>
