    </div>
</template>

<script>
    import "../../assets/css/game.less";
    export default {
        name:'game',
        data(){
            return{
                currentPage:1,
                // 定义页数
                pageNum:'',
                GameData:[],
                recomGameData:[
                    {title:'英雄联盟',url:"https://lol.qq.com/act/a20200819ninthyear/index.html?e_code=507042"},
                    {title:'贪玩蓝月',url:"http://www.code.twzui6.com/htmlcode/9843.html?cplaceid=191314428.602838547.32781377905"},
                    {title:'风暴英雄',url:"https://heroes.blizzard.cn/landing"},
                    {title:'炉石传说',url:"https://hs.blizzard.cn/landing"},
                    {title:'剑客传奇',url:"http://wan2.p0uqu.com/on/yx-xx/xianxia27/d7/d20200814-109/?sg_vid=RCOvrrmZ3Ok6MzZ-uwtVSqFRaBFR6H23y26_ZxX6666666uRvJMR6666666"},
                    {title:'绝地求生',url:"https://pubg.qq.com/"},
                ],
            }
        },
        created(){
            this.getGameData()
        },
        methods:{

            getGameData(pageNum){
                this.axios({
                    method:'GET',
                    url:'/list',
                    params:{
                        apikey: this.appkey,
                        category:'T1348654151579',
                        page:pageNum,
                    }
                }).then((result) =>{
                    // console.log('result',result)
                    this.GameData = result.data.data
                }).catch((err) =>{
                    // console.log('err',err)
                })
            },
             //点击页码，获取对应的val值，在传给要显示的页数内容
            handleCurrentChange(val){
                
                // console.log('val',val)
                let pageNum = val;
                // console.log('pageNum',pageNum)
                //调用请求数据，才能获取值
                this.getGameData(pageNum)
            
            },
            goDetail(id) {
                // console.log(id);
                this.$router.push({ name: "Detail", query: { id } });
            },
        }
    }
</script>

